body {
    width: 100%;
    height: 100vh;
    margin: 0;
    overflow-x: hidden;
}

header {
    position: fixed;
    top: 0;
    left: 0;
    width:  calc(100vw - env(safe-area-inset-left) - env(safe-area-inset-right));
    height: 45px;
    padding-left: env(safe-area-inset-left);
    padding-right: env(safe-area-inset-right);
    padding-top: env(safe-area-inset-top);
    background-color: #229944;
    z-index: 1;
}

header > h1 {
    margin: 0;
}

header > h1 > a {
    color: #ffffff;
    text-decoration: none;
}

hr {
    height: 1px;
    background-color: #e0e0e0;
    border: none;
}

main {
    margin: 0 auto;
    max-width: 800px;
    padding: 60px 10px 10px 10px;
}

table {
    width: 90%;
    margin: 25px auto;
    border-collapse: collapse;
}

tr {
    background-color: #ffffff;
}

tbody > tr:nth-child(odd) {
    background-color: #f7f7f7;
}

th, td {
    padding: 10px 15px;
    text-align: center;
    border: solid 1px #cccccc;
}

.article-link {
    display: block;
    max-width: 600px;
    margin: 15px auto;
    border: solid 1px #999999;
    text-decoration: none;
    color: #000000;
    background-color: #ffffff;
    transition: 0.1s;
    user-select: none;
}

.article-link:hover {
    background-color: #f7f7f7;
}

.article-link:active {
    background-color: #f0f0f0;
}

.article-link-title {
    height: 36px;
    margin: 5px 15px 0 15px;
}

.article-link-upload {
    height: 24px;
    color: #999999;
    margin: 0 15px;
}

.article-link-content {
    height: 24px;
    color: #666666;
    width: calc(100% - 30px);
    margin: 5px 15px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

@keyframes loading {
    from {
        background-color: #cccccc;
    }
    50% {
        background-color: #eeeeee;
    }
    to {
        background-color: #cccccc;
    }
}

.article-link-unload {
    background-color: #cccccc;
    animation: loading linear infinite 2000ms;
}

.code-block {
    background-color: #2a2a2a;
    color: #e0e0e0;
}

.code-block-utils {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 30px;
}

.code-block-infos {
    display: flex;
    flex-direction: row;
}

.code-block-lang,
.code-block-file,
.code-block-copy {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15px;
    background-color: #eeeeee;
    color: #000000;
    user-select: none;
    opacity: 0.5;
    transition: 0.3s;
}

.code-block-lang:hover,
.code-block-file:hover,
.code-block-copy:hover {
    opacity: 1;
}

.code-block-file {
    margin-left: 5px;
}

.code-block-copy {
    cursor: pointer;
}

.code-block > pre {
    margin-top: 0;
    padding: 20px;
    font-size: 16px;
}